import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import { Suspense, lazy, useCallback, useEffect, useState } from "react";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import LmsAllLeaveTeaching from "./LmsAllLeaveTeaching";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  useLmsConfigMaster,
  useLmsExcelAllStaffLeaveCount,
  useLmsUpdateCarryRule,
} from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import UploadExcelButton from "../../../Ui/Button/UploadExcelButton";
const LmsAllLeaveNonTeaching = lazy(() => import("./LmsAllLeaveNonTeaching"));
const LmsAllLeave = ({ leaveTypeToggle, lcid, instituteId, lmid }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [state, setState] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [lmsUpdateCarryRule] = useLmsUpdateCarryRule();
  const [fileUpload] = useFileUpload();
  const [lmsExcelAllStaffLeaveCount] = useLmsExcelAllStaffLeaveCount();
  // const [ctOneExperimentExcelExport] = useCtOneExperimentExcelExport();

  const { lmsConfigMaster } = useLmsConfigMaster({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });

  const onNavigate = useCallback(() => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/lms`
        : `/q/${params.username}/member/staff/lms`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  useEffect(() => {
    if (lmsConfigMaster?.leave_start_academic_year) {
      setState(
        slashDateFormat(
          lmsConfigMaster?.leave_start_academic_year?.split("T")?.[0]
        )
      );
    }
  }, [lmsConfigMaster?.leave_start_academic_year]);
  const onDateGrab = (val) => {
    let modi = ymdSlashDateFormat(val);
    setState(slashDateFormat(modi));

    if (lcid) {
      setDisabled((pre) => !pre);
      lmsUpdateCarryRule({
        lcid: lcid,
        flow: "Leave_Start",
        year: modi ? modi?.substring(0, 4) : "",
        date: modi ? modi?.substring(8, 10) : "",
        month: modi ? modi?.substring(5, 7) : "",
        int_month: modi ? +modi?.substring(5, 7) : "",
        carryRule: { calendar: modi },
      })
        .then(() => {
          // onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onExcelFileUpload = (e) => {
    if (e.target.files[0] && lmid) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            lmsExcelAllStaffLeaveCount({
              lmid: lmid,
              excelFile: {
                excel_file: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };
  // console.info("SAfsdfs", state);

  // const onExport = () => {
  //   if (getQuery.state?.experiment?._id) {
  //     setDisabled((pre) => !pre);
  //     ctOneExperimentExcelExport({
  //       exid: getQuery.state?.experiment?._id,
  //       bodyParameter: {},
  //     })
  //       .then((res) => {
  //         if (res?.data?.excel_key) {
  //           setTimeout(() => {
  //             setDisabled((pre) => !pre);
  //             const newWindow = window.open(
  //               `${cdnUrl}/${res?.data?.excel_key}`,
  //               "_blank",
  //               "noopener,noreferrer"
  //             );
  //             if (newWindow) newWindow.opener = null;
  //           }, 2500);
  //         } else {
  //           setDisabled((pre) => !pre);
  //         }
  //       })
  //       .catch({});
  //   }
  // };

  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div className={style.fm_header_start_container}>
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{t("all_leave")}</h6>
          </div>
          <div
            className={style.fm_header_container}
            style={{
              gap: "1rem",
            }}
          >
            <FormCalendarInput
              label={t("select_leave_academic_year")}
              placeholder={t("select_date")}
              name={"calendar"}
              value={state}
              onChange={onDateGrab}
              customFormInputContainer={{
                flexDirection: "row",
                gap: "1rem",
              }}
            />
            <UploadExcelButton onExcelFileUpload={onExcelFileUpload} />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/lms?type=leave${tab.urlKey}`
                : `/q/${params.username}/member/staff/lms?type=leave${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginBlock: "0",
        }}
      />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {!searchParams.get("n") ? (
          <LmsAllLeaveTeaching
            instituteId={instituteId}
            leaveTypeToggle={leaveTypeToggle}
            lmid={lmid}
          />
        ) : searchParams.get("n") === "nonteaching" ? (
          <Suspense fallback={<QvipleLoader />}>
            <LmsAllLeaveNonTeaching
              instituteId={instituteId}
              leaveTypeToggle={leaveTypeToggle}
              lmid={lmid}
            />
          </Suspense>
        ) : null}
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </UserMemberContainerWrapper>
  );
};

export default LmsAllLeave;

const tabList = [
  {
    label: "teaching_faculty",
    id: "teaching_faculty",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "non_teaching_faculty",
    id: "non_teaching_faculty",
    urlKey: "&n=nonteaching",
    groupIs: ["nonteaching"],
  },
];
